<template>
	<iframe class="danmaku-self" :src="url" />
</template>

<script lang="ts">
import { defineComponent } from "vue";
import { wsevent } from "@front/api";
import { mapState } from "vuex";
import { registerRole } from "@front/util_function/base";
import { getMockByType } from "@front/views/danmakuSetting/mock/index";
import { loginSession } from "@front/api/user";
export default defineComponent({
	name: "webDanmaku",
	data() {
		const statusTimer: any = null;
		return {
			statusTimer,
			appID: "danmakuWeb"
		};
	},
	created() {
		this.$store.state.isLogined = true;
		this.registerEvents();
	},
	mounted() {
		this.statusLooper();
		registerRole("网页端");
		setTimeout(() => {
			this.sendDanmaku({
				mock: true,
				liverUID: 23682490,
				type: 1000,
				data: {
					danmuInfo: {
						sendTime: 1618750176864,
						userInfo: {
							userID: 1,
							nickname: "ACFUN前后端开源⑨课",
							avatar:
								"https://tx-free-imgs.acfun.cn/FllI0yyjNgQ61QdJJdbIh9cRQIYY?imageMogr2/auto-orient/format/webp/quality/75!/ignore-error/1",
							medal: {
								uperID: 23682490,
								userID: 1,
								clubName: "ACER",
								level: 999
							},
							managerType: 0
						}
					},
					content: "弹幕流启动成功！尚未发现弹幕"
				}
			});
		}, 5000);
	},
	beforeUnmount() {
		this.unRegisterEvents();
	},
	computed: {
		url() {
			// @ts-ignore
			const file = this.$route.query.file || "index.html";
			return `http://${window.location.host}/configFiles/selfDanmaku/${file}`;
		},

		...mapState([
			"userSession",
			"danmakuProfile",
			"danmakuSession",
			"roomProfile",
			"streamStatus"
		])
	},
	methods: {
		registerEvents() {
			wsevent.on("sendMockDanmaku", this.setMock);
			wsevent.on("server-response", this.handleResponse);
		},
		unRegisterEvents() {
			wsevent.off("sendMockDanmaku", this.setMock);
			wsevent.off("server-response", this.handleResponse);
		},

		handleResponse(e: any) {
			for (const key in e) this.$store.state[key] = e[key];
			loginSession(e.userSession || {});
			this.checkReconnect();
		},
		async chechEveryThing() {
			try {
				if (!(wsevent.ws?.readyState === 1) || !wsevent.registered) {
					await wsevent.register(this.appID);
				}
				this.subscribeData();
			} catch (error) {
				console.log(error);
			}
			if (
				this.roomProfile.liveID &&
				!["danmakuing", "streaming"].includes(this.streamStatus.step)
			) {
				this.streamStatus.step = "streaming";
				this.$store.dispatch("streaming", this.sendDanmaku);
			}
		},
		subscribeData() {
			wsevent.wsEmit(
				"register-client",
				{
					sourceID: this.appID,
					states: ["userSession", "roomProfile"]
				},
				"server"
			);
		},
		statusLooper() {
			clearInterval(this.statusTimer);
			this.statusTimer = setInterval(() => {
				this.chechEveryThing();
			}, 5000);
		},
		checkReconnect() {
			clearTimeout((this as any).connectTimer);
			(this as any).connectTimer = setTimeout(() => {
				// @ts-ignore
				wsevent.ws.onclose = null;
				wsevent.ws?.close();
			}, 20 * 1000);
		},
		setMock(value: any) {
			const mockDanmaku: any = getMockByType(value);
			this.sendDanmaku(mockDanmaku);
		},
		sendDanmaku(danmaku: any) {
			this.$el.contentWindow.postMessage(JSON.stringify(danmaku));
		}
	}
});
</script>
<style lang='scss'>
@import "@front/styles/variables.scss";
html,
body,
#app {
	background-color: transparent;
	min-width: 0px;
	min-height: 0px;
	width: 100%;
	height: 100%;
	padding: 0px;
}
#app {
	margin: 0px;
	width: 100%;
	height: 100%;
	transition: none;
}
.danmaku-self {
	width: 100%;
	height: 100%;
	border: none;
}
</style>
